import { Time } from '@angular/common';

export class BaseEntity {
    id?: string;
    uid: string;
    version: number;
    createdDate: number;
    lastModifiedDate: number;
    createBy: string;
    lastModifiedBy: string;
}

export interface User {
    uid: string;
    email: string;
    displayName: string;
    emailVerified: boolean;
}

export class Idea extends BaseEntity {
    constructor() {
        super();
        this.name = '';
        this.notes = '';
    }
    name: string;
    notes: string;
}

export class Task extends BaseEntity {
    solarCalendar: Date;
    lunarCalendar: Date;
    caseName: string;
    location: string;
    hall: string;
    event: string;
    jobContent: JobContent;
    arriveHallTime: Time;
    receiveBodyTime: Time;
    casketingTime: Time;
    moveCoffinTime: Time;
    worshipTime: Time;
    staffs: Staffs[];
    remarks: string;
    leader: string;
    isCaseClosed: string;
}

export class JobContent {
    funeral: boolean; // 出殯
    funeralWorkerCount: number;
    inOut: boolean; // 出入現
    inOutWorkerCount: number;
}

export class Staffs {
    name: string;
}
