import { AngularFirestoreCollection, AngularFirestore, DocumentReference } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { Task } from '../@core/data/domain/model';
import { map, take } from 'rxjs/operators';
import { v4 as uuidv4 } from 'uuid';

@Injectable({
  providedIn: 'root'
})
export class TaskService {
  private tasks: Observable<Task[]>;
  private taskCollection: AngularFirestoreCollection<Task>;

  constructor(private angularFirestore: AngularFirestore) {
    this.taskCollection = this.angularFirestore.collection<Task>('tasks');
    this.tasks = this.taskCollection.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          data.id = id;
          return { id, ...data };
        });
      })
    );
  }

  getTasks() {
    return this.tasks;
  }

  getTask(id: string): Observable<Task> {
    return this.taskCollection.doc<Task>(id).valueChanges().pipe(
      take(1),
      map(task => {
        task.id = id;
        return task;
      })
    );
  }

  addTask(task: Task): Promise<DocumentReference> {
    task.uid = uuidv4();
    return this.taskCollection.add(task);
  }

  updateTask(task: Task): Promise<void> {
    return this.taskCollection.doc(task.id).update(task);
  }

  deleteTask(id: string): Promise<void> {
    return this.taskCollection.doc(id).delete();
  }
}
